.button {
  font-family: "Oswald", sans-serif;
  background-color: #68b738;
  color: #ffffff;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  cursor: pointer;
  border: none;

  @media (min-width: @tablet-width) {
    margin: 0;
    margin-right: auto;
    font-size: 20px;
    line-height: 20px;
    font-weight: 400;
  }

  @media (min-width: @desktop-width) {
    padding: 17px 0;
  }
}


.button--toggle {
  background-color: #ffffff;
}

.button--to-chart {
  grid-column-start: 1;
  grid-column-end: 3;
  padding: 10px 0;
  width: 100%;
}

.button--all {
  padding: 10px 0;
  text-decoration: none;
  background-color: #f2f2f2;
  color: #444444;
  text-align: center;
  margin: 0 auto;
  width: 100%;
}

.button--to-chart-extras {
  @media (min-width: 1200px) {
    width: 224px;
  }
}

.button--submitt-form-button {
  border: none;
  padding: 10px 0;
  margin-bottom: 21px;

  @media (min-width: @tablet-width) {
    width: 282px;
    margin-right: 61px;
  }

  @media (min-width: @desktop-width) {
    width: 568px;
    margin-right: 81px;
  }
}

.button--nutrition-link {
  display: block;
  padding: 11px 0;
  max-width: 240px;
  margin-bottom: 20px;
  position: relative;
  z-index: 1;

  @media (max-width: @tablet-width) {
    margin: 0 auto;
  }
}
